[data-aos="h2-animation"] {
    max-width: 0;
    opacity: 0;
    padding: 6px;
    overflow: hidden;
    text-overflow: clip;
    white-space: nowrap;
    transition-property: max-width, opacity, -webkit-transform;
    transition-property: transform, max-width, opacity;
    transition-property: transform, max-width, opacity, -webkit-transform;
  
  }
  
  
  [data-aos="h2-animation"].aos-animate {
      max-width: 100%;
      opacity: 1;
  }
* {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
  /* box-sizing: border-box; */
}

@font-face {
  font-family: 'Steiner';
  src: url(/fitabo/static/media/Steinerlight.3a0d7821.ttf) format('truetype');
}

@font-face {
    font-family: 'Graphik';
    src: url(/fitabo/static/media/Graphik-Bold.bf600a6e.ttf) format('truetype');
  }
  
.serviceWrapper {
  padding: 80px 0;
  /* margin: 0; */
  /* padding: 0rem calc((100vw - 1000px) / 3); */
}
.row {
  display: grid;
  grid-template-columns: 2fr;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
  padding: 32px 24px;
}

.image {
  width: 100%;
  justify-self: center;
}
.details {
  justify-self: center;
  text-align: center;
  padding: 24px 0;
  font-family: "Katibeh", cursive; 

}
.details h2 {
  font-size: clamp(3rem, 5vw, 5.5rem);
  padding-bottom: 24px;
  font-family: "Inter", sans-serif;
  /* font-family: "Katibeh", cursive; */
    /* font-weight: lighter; */
letter-spacing: 2px;
  /* text-transform: capitalize; */

}
.details p {
  width: 100%;
  letter-spacing: 1.5px;
  line-height: 24px;
  font-size: clamp(1rem, 5vw, 2rem);
  /* font-family: "Roboto", cursive; */
  font-family: "Inter"; 
  font-weight: 200;

}

@media screen and (min-width: 768px) {
  .row {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 45vh 1fr;
    /* grid-gap: 2rem; */
    padding: 24px calc((100vw - 680px) / 2);
  }
  .reverse {
    direction: rtl;
    text-align: left;
  }
  .details p {

    letter-spacing: 1.5px;
    line-height: 3rem;
    /* font-size: clamp(1rem, 3.2vw, 2rem); */
  }
  
}

@media screen and (min-width: 900px) {
  .row {
    grid-template-columns: 1fr 1fr;
    /* grid-gap: 6vw; */
    padding: 24px calc((100vw - 900px) / 2);
  }

  .details p {

    letter-spacing: 1.5px;
    /* line-height: 28px; */
    /* font-size: clamp(1rem, 3.2vw, 2rem); */
  }
}

@media screen and (min-width: 1200px) {
  .row {
    grid-gap: 4vw;
    grid-template-rows: 70vh 1fr;
    padding: 24px calc((100vw - 1200px) / 3);
  }
  .details h2 {
    font-size: clamp(1rem, 5vw, 5.5rem);
    font-weight: bold;
  }
  .details p {
    /* font-size: clamp(1rem, 2vw, 2rem); */
    letter-spacing: 2px;
    line-height: 38px;
  }
}


.faq-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.faq-wrapper > h1 {
  width: 90%;
  padding-bottom: 48px;
  font-size: clamp(2rem, 5vw, 3rem);
}
.faq-wrapper > div {
  position: relative;
  /* margin: 0 16px; */
  width: 90%;
}

input {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: 0;
}

.faq-wrapper > div > h2 {
  position: relative;
  display: block;
  background-color: #180d43;
  color: #fff;
  border-radius: 12px;
  z-index: 3;
  /* font-size: 2.5vw; */
}
.faq-wrapper > div > h2 > label::after {
  content: "\276F";
  width: 28px;
  /* height: 56px; */
  text-align: center;
  /* margin-top: -42px; */
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  transition: all 0.3s;
}
.faq-wrapper > div > h2 > label {
  /* min-height: 68px; */
  cursor: pointer;
  position: relative;
  display: -webkit-flex;
  display: flex;
  padding: 18px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-box-pack: justify;
}

.ans {
  max-height: 0;
  overflow: hidden;
  -webkit-transform: translateY(-3rem);
          transform: translateY(-3rem);
  background-color: #a3a1d0;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  transition: all 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0.2, 3, 0.2);
  padding: 18px;
}

.ans p {
  /* font-size: 1.5vw; */
  padding: 4px;
}
.ans ul{
  margin-left: 18px;
}
input:checked ~ .heading > label::after {
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}
input:checked ~ .ans {
  max-height: 1000px;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  margin: 1rem 0;
}

@media screen and (min-width: 768px) {
  .faq-wrapper {
    padding: 0 calc((100vw - 700px) / 2);
  }
  .ans {
    font-size: clamp(1rem, 3vw, 2rem);
  }
}

@media screen and (min-width: 900px) {
  .faq-wrapper {
    padding: 0 calc((100vw - 900px) / 2);
  }
  .faq-wrapper > div > h2 {
    font-size: clamp(1rem, 3vw, 2rem);
  }
  .faq-wrapper > div > h2 > label {
    padding: 24px;
  }
}

@media screen and (min-width: 1200px) {
  .faq-wrapper {
    padding: 0 calc((100vw - 1200px) / 3);
  }
}

.footer {
    width: 100%;
    padding: 24px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    color: #A3A1D0;
  background-color: #180d43;
}
.footer .follow-us {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;

}
.footer .follow-us svg {
    width: 24px;
    padding: 0 12px;
    fill: #A3A1D0;
    transition: all ease-in-out .3s;
}

.follow-us svg:hover {
    fill: #fff;
    cursor: pointer;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.contact{
    width: inherit;
    display: grid;
    grid-template-columns: 1fr;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    background: url(data:image/png;base64,) top no-repeat;
    background-size: cover;
    position: relative;
    padding: 0rem calc((100vw - 1300px) / 3);

}

.contact img{
    width: 100%;
}
.contact h1{
    font-size: clamp(2rem, 6vw, 3rem);
    color: #fff;
    padding: 24px 24px 0 24px;
}
.rows{
    width: inherit;
    display: grid;
    padding: 16px;
    grid-template-columns: 1fr;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.rows button{
    background-color: #fff;
    padding: 16px 24px;
    border-radius: 12px;
    font-size: 18px;
    margin: 0 auto 24px auto;
    color: #180d31;
    outline: none;
    border: none;

}
.rows button:hover{
    cursor: pointer;
    background-color: #A3A1D0;
    margin-top: -5px;
    transition: all 0.225s ease-in-out;
}
.contact-info div{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #A3A1D0;
    font-size: 18px;
    align-items: center;
}
.contact-info span{
    font-size: 32px;
    padding: 0 16px 8px 0;
}
.form-group div{
    width: 100%;
    display: -webkit-flex;
    display: flex;
}
.contact input, textarea{
    position: relative;
    width: 100%;
    background-color: #A3A1D0;
    padding: 16px;
    margin: 12px auto;
    border: none;
    border-radius: 4px;
    opacity: 1;
    font-size: 1rem;
  }
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
    color: #180d43;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder{
    color: #180d43;
}
input::placeholder, textarea::placeholder{
    color: #180d43;
}
 input:focus, textarea:focus {
    /* font-size: 0.75em; */
    color: #180d43;
    top: -5px;
    transition: all 0.225s ease-in-out;
    outline: #999;
}


@media only screen and (min-width: 768px){
    .contact{
        padding: 0rem calc((100vw - 700px) / 3);    
    }
    .contact img{
        width: 90%;
    }
    .rows{
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 24px;
    }
}



@media only screen and (min-width: 900px){
    .contact{
        padding: 0rem calc((100vw - 700px) / 3);    
    }
    .contact img{
        width: 90%;
    }
}



@media only screen and (min-width: 1200px){
    .contact{
        grid-template-columns: 1fr 1fr;
        padding: 0rem calc((100vw - 1300px) / 3);
        padding-top: 9rem!important;
    
    }
    .contact img{
        width: 90%;
    }
    .rows{
        width: inherit;
        display: grid;
        padding: 16px;
        grid-template-columns: repeat(2, 1fr);
        -webkit-justify-content: space-between;
                justify-content: space-between;
        grid-gap: 24px;
    }
}

